}

.TaSalaryFormChildHeader {}

.TaSalaryFormChildHeader span {
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 1px;
    margin-top: 10px;
    background: white;
    padding: 5px;
}

.TaSalaryFormChildHeaderData span {
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 1px;
    margin-top: 10px;
    background: rgba(255, 255, 255, 0.63);
    padding: 5px;

}

.TaSalaryFormChildHeaderData span input {
    border-radius: 4px;
    font-size: 12px;
    border: 1px solid darkgray;
    color: gray;

}

.TaSalaryFormChildHeaderData span input:focus {
    outline: none;

}


.SalResponse{
    overflow-x: scroll;
}

.SalResponse::-webkit-scrollbar {
    height: 4px;
}

/* Thumb styles */
.SalResponse::-webkit-scrollbar-thumb {
    background-color: #014f86;
    border-radius: 4px;
}

/* Track styles when the thumb is being hovered */
.SalResponse::-webkit-scrollbar-thumb:hover {
    background-color: #014f86;
}

/* Track styles when the scrollbar is in its inactive state */
.SalResponse::-webkit-scrollbar-track {
    background-color: transparent;
}